'use client';

import { ShinyButton } from '@/components/ui/shiny-button';
import { useEffect } from 'react';

export default function HeroSection() {
  const titleWords = [
    "Transform",
    "PDFs",
    "into",
    "interactive",
    "flipbooks"
  ];

  useEffect(() => {
    // Load Unicorn Studio script
    if (!(window as any).UnicornStudio) {
      (window as any).UnicornStudio = { isInitialized: false };
      const script = document.createElement('script');
      script.src = 'https://cdn.jsdelivr.net/gh/hiunicornstudio/unicornstudio.js@v1.4.29/dist/unicornStudio.umd.js';
      script.onload = () => {
        if (!(window as any).UnicornStudio.isInitialized) {
          (window as any).UnicornStudio.init();
          (window as any).UnicornStudio.isInitialized = true;
        }
      };
      (document.head || document.body).appendChild(script);
    }
  }, []);

  return (
    <>
      <section className="relative z-10 flex min-h-screen items-center justify-center px-4 sm:px-6 lg:px-8 pt-20">
        {/* Unicorn Studio Background */}
        <div className="aura-background-component top-0 w-full -z-10 absolute h-full">
          <div data-us-project="EET25BiXxR2StNXZvAzF" className="absolute w-full h-full left-0 top-0 -z-10"></div>
        </div>
        <div className="max-w-4xl mx-auto w-full">
          <div className="space-y-8 text-center">
            {/* Title */}
            <div className="space-y-6">
              <h1 className="text-3xl sm:text-4xl md:text-5xl lg:text-6xl font-semibold text-white leading-tight" style={{ fontFamily: 'var(--font-inter), Inter, sans-serif', letterSpacing: '-0.05em' }}>
                Transform PDFs into <br /> interactive flipbooks
              </h1>

              <p className="mx-auto max-w-2xl text-xs sm:text-sm md:text-base text-gray-400" style={{ fontFamily: 'Inter, sans-serif', letterSpacing: '-0.02em' }}>
                Create engaging digital publications with powerful analytics
              </p>
            </div>

            {/* CTA Button */}
            <div className="flex flex-col items-center gap-3 mt-10">
              <ShinyButton onClick={() => window.location.href = '/pricing'}>
                Start Creating Free
              </ShinyButton>

              <p className="text-xs text-gray-400" style={{ fontFamily: 'var(--font-inter), Inter, sans-serif' }}>
                No Credit Card Required
              </p>
            </div>
          </div>
        </div>
      </section>

      {/* Next Section */}
      <section className="md:px-12 bg-white pt-32 pr-6 pb-32 pl-6">
        <div className="text-center max-w-5xl mr-auto ml-auto">
          <style>
            {`
              .k-word {
                opacity: 0.2;
                transition: opacity 0.25s ease;
                display: inline-block;
              }

              .k-word.active {
                opacity: 1;
              }
            `}
          </style>

          <span className="inline-block text-neutral-400 text-xs font-medium tracking-[0.2em] uppercase mb-8">
            [ The Collective ]
          </span>

          <h2 className="karaoke-container text-2xl md:text-4xl leading-tight font-normal text-neutral-800 mb-10 tracking-tight">
            We are a collective of dermatologists and designers obsessed with clarity. Rejecting the noise of the industry,
            we engineer solutions that blend clinical precision with botanical purity to
            <span className="italic text-neutral-400 font-serif">nourish and transcend</span>.
          </h2>

          <p className="text-xs text-neutral-400 uppercase tracking-widest mt-12">
            Welcome to the new standard
          </p>

          <script>
            {`
              (function () {
                const container = document.querySelector('.karaoke-container');
                if (!container || container.dataset.processed) return;

                const wrapWords = (node) => {
                  if (node.nodeType === 3) {
                    const words = node.textContent.match(/\S+|\s+/g) || [];
                    const fragment = document.createDocumentFragment();

                    words.forEach((word) => {
                      if (word.trim().length > 0) {
                        const span = document.createElement('span');
                        span.textContent = word;
                        span.className = 'k-word';
                        fragment.appendChild(span);
                      } else {
                        fragment.appendChild(document.createTextNode(word));
                      }
                    });

                    node.replaceWith(fragment);
                  } else if (node.nodeType === 1) {
                    Array.from(node.childNodes).forEach(wrapWords);
                  }
                };

                Array.from(container.childNodes).forEach(wrapWords);
                container.dataset.processed = 'true';

                const observer = new IntersectionObserver(
                  (entries) => {
                    entries.forEach((entry) => {
                      if (entry.isIntersecting) {
                        const words = container.querySelectorAll('.k-word');

                        words.forEach((word, index) => {
                          setTimeout(() => {
                            word.classList.add('active');
                          }, index * 40); // speed of the karaoke effect
                        });

                        observer.unobserve(container);
                      }
                    });
                  },
                  { threshold: 0.2 }
                );

                observer.observe(container);
              })();
            `}
          </script>
        </div>
      </section>
    </>
  );
}
